import React, { useState } from 'react';
import { FaTimes } from 'react-icons/fa';

/**
 * A hook that makes a badge dismissable by adding an X button
 * 
 * @param {Object} options - Configuration options
 * @param {Function} options.onDismiss - Callback function called when badge is dismissed
 * @param {number} options.iconSpacing - Space between the X icon and badge text (default: 7)
 * @param {Object} options.iconStyle - Additional styles for the X icon
 * @returns {Object} - Object containing the dismissable props to spread on a Badge component
 */
const useDismissableBadge = ({ 
  onDismiss, 
  iconSpacing = 7,
  iconStyle = {}
} = {}) => {
  const [visible, setVisible] = useState(true);

  const handleDismiss = (e) => {
    e.stopPropagation(); // Prevent triggering the badge's onClick
    setVisible(false);
    if (onDismiss) {
      onDismiss();
    }
  };

  // The X icon component
  const dismissIcon = (
    <span 
      onClick={handleDismiss}
      style={{
        cursor: 'pointer',
        display: 'inline-flex',
        alignItems: 'center',
        justifyContent: 'center',
        marginLeft: iconSpacing,
        ...iconStyle
      }}
      aria-label="Dismiss"
      role="button"
      tabIndex={0}
    >
      <FaTimes size={12} />
    </span>
  );

  // Props to spread on the Badge component
  const dismissableProps = {
    icon: dismissIcon,
    iconPosition: 'end',
    iconSpacing,
    style: { display: visible ? 'inline-flex' : 'none' },
    interactive: true,
  };

  return {
    visible,
    dismissableProps,
    dismiss: () => setVisible(false)
  };
};

export default useDismissableBadge; 