import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";

// Avatar variants matching the project's theme collections
const AVATAR_VARIANTS = {
  // Basic variants
  BASIC: ["primary", "secondary", "tertiary", "basic"],
  // Lebanese-inspired collections
  CEDAR: ["cedar", "cedar-light", "cedar-dark"],
  MEDITERRANEAN: ["sea", "coast", "azure"],
  MOUNTAIN: ["stone", "earth", "terracotta"],
  BEIRUT: ["urban", "concrete", "steel"],
  CULINARY: ["olive", "saffron", "grape"],
  FESTIVAL: ["festive", "celebration", "heritage"],
  CRAFT: ["copper", "silver", "ceramic"],
  MODERN: ["tech", "digital", "startup"],
};

// Avatar sizes
const AVATAR_SIZES = {
  SMALL: "small",
  MEDIUM: "medium", // Default
  LARGE: "large",
  XLARGE: "xlarge",
};

// Avatar shapes
const AVATAR_SHAPES = {
  CIRCLE: "circle", // Default
  SQUARE: "square",
  ROUNDED: "rounded",
};

// Extract initials from a name string
const getInitials = (name) => {
  if (!name) return "?";

  return name
    .split(" ")
    .map((part) => part[0])
    .join("")
    .substring(0, 2)
    .toUpperCase();
};

// Get all possible variants as a flat array for PropTypes
const getAllVariants = () => {
  return Object.values(AVATAR_VARIANTS).flat();
};

const Avatar = ({
  src,
  name,
  alt,
  size = AVATAR_SIZES.MEDIUM,
  shape = AVATAR_SHAPES.CIRCLE,
  variant = "basic",
  className = "",
  backgroundColor,
  textColor,
  borderColor,
  borderWidth,
  borderRadius,
  onClick,
  ...props
}) => {
  const [imgError, setImgError] = useState(false);
  const [loading, setLoading] = useState(!!src);
  const [randomVariant, setRandomVariant] = useState(null);
  const initials = getInitials(name);

  // Reset loading and error states when src changes
  useEffect(() => {
    if (src) {
      setLoading(true);
      setImgError(false);
    } else {
      setLoading(false);
    }
  }, [src]);

  // Select a random variant when "random" is specified
  useEffect(() => {
    if (variant === "random") {
      // Flatten all variants into a single array
      const allVariants = getAllVariants();
      // Select a random variant from the array
      const randomIndex = Math.floor(Math.random() * allVariants.length);
      setRandomVariant(allVariants[randomIndex]);
    }
  }, [variant]);

  // Handle image loading error - fall back to initials
  const handleImgError = () => {
    setImgError(true);
    setLoading(false);
  };

  // Handle image loading success
  const handleImgLoad = () => {
    setLoading(false);
  };

  // Determine which variant to use (regular or random)
  const effectiveVariant = variant === "random" ? randomVariant : variant;

  // Construct the base CSS classes
  const avatarClasses = [
    "lebify-avatar",
    `lebify-avatar--${size}`,
    `lebify-avatar--${shape}`,
    effectiveVariant &&
      effectiveVariant !== "none" &&
      `lebify-avatar--${effectiveVariant}`,
    loading && "lebify-avatar--loading",
    className,
  ]
    .filter(Boolean)
    .join(" ");

  // Custom inline styles
  const customStyles = {
    ...(backgroundColor && { "--avatar-bg-color": backgroundColor }),
    ...(textColor && { "--avatar-text-color": textColor }),
    ...(borderColor && { "--avatar-border-color": borderColor }),
    ...(borderWidth && { "--avatar-border-width": borderWidth }),
    ...(borderRadius && { borderRadius }),
  };

  return (
    <div
      className={avatarClasses}
      style={customStyles}
      onClick={onClick}
      role={onClick ? "button" : undefined}
      tabIndex={onClick ? 0 : undefined}
      {...props}
    >
      {src && !imgError ? (
        <>
          <img
            src={src}
            alt={alt || name || "Avatar"}
            onError={handleImgError}
            onLoad={handleImgLoad}
            className="lebify-avatar__img"
          />
          {loading && <div className="lebify-avatar__loader"></div>}
        </>
      ) : (
        <span className="lebify-avatar__initials">{initials}</span>
      )}
    </div>
  );
};

Avatar.propTypes = {
  /** Image source URL */
  src: PropTypes.string,
  /** User name (used for extracting initials and alt text) */
  name: PropTypes.string,
  /** Alternative text for the avatar image */
  alt: PropTypes.string,
  /** Avatar size */
  size: PropTypes.oneOf(Object.values(AVATAR_SIZES)),
  /** Avatar shape */
  shape: PropTypes.oneOf(Object.values(AVATAR_SHAPES)),
  /** Avatar variant/style */
  variant: PropTypes.oneOf([...getAllVariants(), "random", "none"]),
  /** Additional CSS classes */
  className: PropTypes.string,
  /** Background color (CSS value) */
  backgroundColor: PropTypes.string,
  /** Text color for initials (CSS value) */
  textColor: PropTypes.string,
  /** Border color (CSS value) */
  borderColor: PropTypes.string,
  /** Border width (CSS value with unit) */
  borderWidth: PropTypes.string,
  /** Border radius (CSS value with unit) */
  borderRadius: PropTypes.string,
  /** Click handler */
  onClick: PropTypes.func,
};

// Export constants for external use
export { AVATAR_VARIANTS, AVATAR_SIZES, AVATAR_SHAPES };

export default Avatar;
